import React from 'react';
import { Container, List, Segment, Button, Icon } from 'semantic-ui-react';

const Footer = () => (
    <div>
        <Segment inverted style={{ margin: '2em 0em 0em', padding: '2.5em 0em' }} vertical>
            <Button floated='right' basic inverted color='red' onClick={() => window.scrollTo(0, 0)}>
                <Icon name='arrow up' />Back to top
            </Button >
        </Segment>
        <Segment inverted vertical>
            <Container textAlign='center'>
                <List horizontal inverted divided link >
                    <List.Item as='a' href='#'> Develop with React js </List.Item>
                    <List.Item as='a' href='#'> Copyright 2019 </List.Item>
                </List>
            </Container>
        </Segment>
    </div>
)
export default Footer;


